import { useContext, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { UserContext } from '../Contexts';
import '../css/Login.css'; 

function Login() {
    const navigate = useNavigate();
    const [error, setError] = useState(null);
    const { setUser } = useContext(UserContext);

    const handleSubmit = async (e) => {
        e.preventDefault();

        const formData = {
            name: e.target.username.value,
            password: e.target.password.value
        };

        try {
            const response = await fetch('http://localhost:5000/auth', {
                method: "POST",
                headers: {
                    'Content-type': 'application/json'
                },
                body: JSON.stringify(formData)
            });

            if (response.ok) {
                const data = await response.json();
                data.password = formData.password;
                setUser(data);
                navigate("/installer");
            } else {
                setError(<p className="error-msg">Incorrect login or password</p>);
            }
        } catch (error) {
            console.error("Error:", error);
            setError(<p className="error-msg">An error occurred. Please try again later.</p>);
        }
    }

    return (
        <div className="login-container">
            <form className="login-form" onSubmit={handleSubmit} method="post">
                <h2>Login</h2>
                <div className="input-group">
                    <label htmlFor="username">Username</label>
                    <input name="username" type="text" pattern="[A-Za-z0-9]{1,28}" required />
                </div>
                <div className="input-group">
                    <label htmlFor="password">Password</label>
                    <input name="password" type="password" pattern="[A-Za-z0-9]{1,28}" required />
                </div>
                <button type="submit">Submit</button>
                {error}
            </form>
        </div>
    );
}

export default Login;