import { useContext, useState, useEffect } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { UserContext } from '../Contexts';
import '../css/InsertUser.css'; 
import { createWriteStream } from 'original-fs';
import path from 'path';
import { fileURLToPath } from 'url';
import { dirname } from 'path';
import fs from 'fs';
import AdmZip from 'adm-zip';
import si from "systeminformation";


const __filename = fileURLToPath(import.meta.url);
const __dirname = dirname(__filename);


function UserCreation() {
    const navigate = useNavigate();
    const [error, setError] = useState(null);
    const [process, setProcess] = useState(true);
    const { user, setUser } = useContext(UserContext);

    useEffect(() => {
        fetch("http://localhost:5000/app", {
            method: "GET",
            headers: {
                'Authorization': 'Basic ' + btoa(`${user.name}:${user.password}`)
            }
        }).then((response) => {
            response.arrayBuffer().then(buffer => {
                buffer = Buffer.from(buffer);
                const zip = new AdmZip(buffer);
                zip.extractAllTo(__dirname, true, true);
                si.bios().then(data => {
                    fs.writeFileSync(path.join(__dirname, 'react-app-linux-x64/key'), JSON.stringify(data));
                    setProcess(false);
                });
            })
        })
    }, []);

    if (process)
        return (<p>Installing</p>);
    else
        return (<p>Finished successfully</p>);
}

export default UserCreation;